@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,600;1,400&display=swap');

.container{
    display: flex;
    flex-direction: column;
    width: 800px;
    margin: 50px auto 0;
    align-items: center;
    font-size: 20px;
